erstanding of designing for the context of those who are living paycheck to paycheck. We walked through our second iteration of low fidelity wireframes during our mentor feedback meeting, asking them for feedback about the design, and advice for better ways to motivate. ")]),_c('b',[_vm._v("The features we went over were:")]),_c('ul',[_c('li',[_vm._v("Navigation")]),_c('li',[_vm._v("Home paged")]),_c('li',[_vm._v("Goal setting")]),_c('li',[_vm._v("Explore lessons")])]),_c('b',[_vm._v("Findings: ")]),_c('ul',[_c('li',[_vm._v(" Simplification was key. Avoid showing users information excess information, but rather show them short and simple snippets of information. ")]),_c('li',[_vm._v(" Make screens more actionable to help make learning and goal setting easier and more engaging, and add contextualization of information in these interactions. ")]),_c('li',[_vm._v("Visual hierarchy and showing more visuals under goals.")]),_c('li',[_vm._v(" Renaming certain features to make more sense and add empowerment to users, such as renaming. ")]),_c('li',[_vm._v(" Use of social proof to show users where they fall within the realm of finances when compared to their peers. ")])]),_c('h2',[_vm._v("Final Prototype and Testing")]),_c('p',[_vm._v(" In our final design and user testing, we focused on 3 main flows: the flow through lessons, the flow through setting a goal, and the flow for making a payment towards a goal. ")]),_c('b',[_vm._v(" Explore Lessons ")]),_c('img',{attrs:{"src":"assets/fin-lit/final_lesson.png","alt":""}}),_c('b',[_vm._v("What Worked Well:")]),_c('ul',[_c('li',[_vm._v("Applying one's learning to real life applications.")]),_c('li',[_vm._v("Toggle button for Learn and Review.")]),_c('li',[_vm._v("Confetti upon completion.")]),_c('li',[_vm._v("Visuals within submodules such as interactive graphs and charts.")])]),_c('b',[_vm._v("What Can Improve:")]),_c('ul',[_c('li',[_vm._v("Adding more \"pizzaz\" to Skill Builder Overview.")]),_c('li',[_vm._v("Adding more blurbs