nt through several iterations and transitions along the way. One of the biggest areas for growth that we came across in our end product was the quality and depth of the lessons. To better develop these lessons and ideas, an important next step would be to focus on UX writing and content strategy to more effectively present the financial literacy content to our users. Although the content we present is helpful, it may not be portrayed in the cleanest way, and there is definitely room for improvement. ")]),_c('p',[_vm._v(" We also ended the project with a user testing session. Although we got decent reviews from our users, there was still a large amount of feedback which would help us improve our application. I am most excited about the inclusion of how much money has been put towards your goal at the top of the goal details page to show how much progress has been made towards the goal to motivate the user to make more progress towards their goals. There were improvements to be made to the lessons within our application. Although the content was solid, the layout could use more reworking. We would add more color and engagement factors within the application, and would work to make the screens full of less text. ")]),_c('p',[_vm._v(" If we were continued with this project, we would also want to create more screens and flows for our user's learning experience such as the prototyping out the savings flow, budgeting flow, custom goal flow, and more content for the lessons that are available in the skill builder section. We then, obviously, would perform more user testing and iterations upon our product with users who are working paycheck to paycheck. ")]),_c('h2',[_vm._v("Reflection")]),_c('p',[_vm._v(" This capstone project was a great experience. I was worried at the start of this project to be designing for financial literacy when I really did not know very much about financial literacy myself. During our interview process, our intended user groups had just about as much financial literacy awareness as we did. One of the biggest barriers, especially among the college students, was the lack of stable income as a student. Our interviews with the older generation of users, had similar insights about what happens when you neglect financial literacy, and it also gave me insight into the pains associated with debt, especially for those in lower incomes. It was in our interview with the financial professor at UW that we were able to start to learn financial literacy, which was a key facet to having successful designs. Personally, I talked to my dad about financial literacy for the first time during this project. He seemed really excited that I was interested in learning financial literacy, and I'm grateful that this project allowed me to increase my financial literacy skills. It also motivated me to continue my own financial literacy journey after this project is complete. ")]),_c('div',{staticClass:"\n      d-flex\n      flex-column flex-md-row\n      text-center\n      justify-content-center justify-content-lg-end\n    "},[_c('router-link',{attrs:{"to":"/on-trak"}},[_c('b',[_vm._v("Next Project: ")]),_vm._v("OnTrak Troubleshooting")])],1)])}
