var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('PortfolioPiece',[_c('div',{staticClass:"title"},[_c('h1',[_vm._v("Night Owl")]),_c('p',{staticClass:"subhead"},[_vm._v("Keeping Students Safe At Night")]),_c('hr')]),_c('p',[_vm._v(" This project spanned 11 weeks in our HCDE 318 (Introduction to User Centered Design) course, in Autumn 2019. As a team of 4, we applied the user centered design process to the problem area of the danger of walking alone at night for students. The process culminated in a mobile application called Night Owl that gives students confidence and control when walking alone at night. ")]),_c('h2',[_vm._v("PROBLEM STATEMENT")]),_c('p',[_vm._v(" University District is known to be a sketchy neighborhood. For example, the Safeway northwest of campus is fondly called “Sketchway” by students. This brings up the concern that walking near campus at night can be dangerous for students; students should not have to be concerned with their safety when walking around their university. This problem resonates with our team and peers, which is why we wanted to tackle the area of how to help students feel safe and confident walking near UW’s campus during night.” ")]),_c('h2',[_vm._v("INTERVIEWS")]),_c('p',[_vm._v(" Our research consisted of three different methods: semi-structured interview, “fly on the wall” observation, and competitive analysis. Through these methods, we learned more about our user group and their goals and pain points as well as other products already on the market. Each team member conducted a one-on-one interview with a student at the University of Washington that walks during dusk or at night at least 4 times a month. We then regrouped and synthesized our findings into key takeaways that would help us define our problem space and user group. ")]),_c('h3',[_vm._v("Key Takeaways:")]),_c('ul',[_c('li',[_vm._v(" They felt more comfortable talking with a friend on the phone when walking alone ")]),_c('li',[_vm._v("Walking home alone is unavoidable")]),_c('li',[_vm._v("They know about UW safety, but doesn’t know how it works")]),_c('li',[_vm._v("They are very aware of their surroundings")])]),_c('h3',[_vm._v("Individual Interviews:")]),_c('ul',[_c('li',[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1VaVEy4UpKD1jpLRqlOqRArkhunDgciJn/view","target":"_blank"}},[_vm._v("Maya's Interviews")])]),_c('li',[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1CZLo_7Qzbt-jNqwxZFSeKI0GEpFxEXrz/view","target":"_blank"}},[_vm._v("Valerie's Interviews")])]),_c('li',[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1fU0MwbjINa_bEsWx1NxDP3ozXNGpl8Gb/view","target":"_blank"}},[_vm._v("Peter's Interviews")])]),_c('li',[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1oPi_3ShrLl2moqErynGaPI06bK3blrU5/view","target":"_blank"}},[_vm._v("Lia's Interviews")])])]),_c('h2',[_vm._v("OBSERVATIONS")]),_c('p',[_vm._v(" Next, as a team we performed a “fly on the wall” observation on the Ave. The goal of this study was to observe and investigate the actions and behaviors of students walking in the U District at night. ")]),_c('img',{attrs:{"src":"assets/night-owl/street.png","alt":"the corner of 45th and University Ave in Seattle's University District"}}),_c('h3',[_vm._v("Key Findings:")]),_c('ul',[_c('li',[_vm._v("Users were interacting with their phone in some way")]),_c('li',[_vm._v("Users walking alone didn’t seem scared")]),_c('li',[_vm._v("Many users wore earbuds")])]),_c('a',{staticClass:"yeet",attrs:{"href":"https://drive.google.com/file/d/1bnC4-I8SMbEtcSESTvVFnjK3JHV3tf9V/view","target":"_blank"}},[_vm._v("Find the full observation document here")]),_c('h2',[_vm._v("COMPETITIVE ANALYSIS")]),_c('p',[_vm._v(" Each team member conducted one competitive analysis on an existing safety app. This allowed us to understand how existing products on the market meet users’ needs and how they could be improved. We analyzed the apps BSafe, Circle of 6, Sekura, and Noonlight. Overall, the apps all had streamlined designs that generally made users feel safe, but also had shortcomings such as confusing features and allowing users to easily trigger emergency services on accident. We used this information to seek opportunities, inspire our designs, and learn what to avoid. ")]),_c('ul',[_c('li',[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1sDMEpPfBFo0-krbwmo9DoYzp0f8WAvhV/view","target":"_blank"}},[_vm._v("Sekura")])]),_c('li',[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1tvpkDkMTISZ-MNfCNf2zx9QFNRNRkt-U/view","target":"_blank"}},[_vm._v("Circle of 6")])]),_c('li',[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1DqlQuCGTVTGIyUZcVeH58XN7XZwvyxlB/view","target":"_blank"}},[_vm._v("Noonlight")])]),_c('li',[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1aQa9VEE2RjTCpqlPM8jPYKB7LMkwHcLN/view","target":"_blank"}},[_vm._v("BSafe")])])]),_c('h2',[_vm._v("USER PERSONAS")]),_c('p',[_vm._v(" From our interviews, we gathered key insights on our users that we compiled into categories such pain points, desires, and goals. These helped us create personas grounded in data in order to avoid stereotypes and assumptions. We chose to create two personas: one that was more experienced with the U-District area and one that was only semi-familiar with the area. This let us understand different user perspectives. Our personas were a home base that we frequently visited to realign our product and goals to our users’ needs. ")]),_c('img',{attrs:{"src":"assets/night-owl/amanda.png","alt":"Amanda Persona"}}),_c('img',{attrs:{"src":"assets/night-owl/mallory.png","alt":"Mallory Persona"}}),_c('a',{attrs:{"href":"https://drive.google.com/file/d/1ezL8OijzPyig5U1rsJ2Ad_570KwMN8wk/view","target":"_blank"}},[_vm._v("Find the full persona document here")]),_c('h2',[_vm._v("USER JOURNEY MAPS")]),_c('p',[_vm._v(" Our user journey maps represent the experiences of Amanda and Mallory, two of our users, while walking home at night. We captured key touch points between her and her environment and emotions throughout her walk home. We focused on anxiety, alertness, and confidence as the emotions we wanted to track, because they were the most prevalent emotions of our interviewees. This journey map helped us understand our user’s feelings and interactions while walking home. ")]),_c('img',{attrs:{"src":"assets/night-owl/amanda-map.jpeg","alt":"amanda map"}}),_c('img',{attrs:{"src":"assets/night-owl/mallory-map.jpeg","alt":"mallory map"}}),_c('h2',[_vm._v("DESIGN REQUIREMENTS")]),_c('p',[_vm._v(" After better understanding our users and problem space through research, we defined design requirements that outlined the functions of our application. These requirements ensured that our solution solved user pain points and satisfied their goals. In addition, they helped us decide to focus on creating a phone app as our final product because it would be able to fulfill the requirements. This would also be convenient for our users since we observed that many of them interacted with their phone in some way while walking at night. ")]),_c('img',{attrs:{"src":"assets/night-owl/aoc.png","alt":"action object context"}}),_c('img',{attrs:{"src":"assets/night-owl/dfc.png","alt":"data function contextual"}}),_c('a',{attrs:{"href":"https://drive.google.com/file/d/1ahZiK5IC7KIrzcDx4orITnnQlw1ixcCK/view","target":"_blank"}},[_vm._v("Find the full design requirements document here")]),_c('h2',[_vm._v("STORYBOARDS")]),_c('p',[_vm._v(" Each team member created two storyboards: one sketched and one photo or photo hybrid. Each storyboard showed a specific scenario of our user interacting with our potential product. It helped us visualize how our user would interact with our potential solution. We chose to have each team member focus on different user scenarios in order for us to understand the full picture of the interactions between the user and our product from the start to end of their walk. ")]),_c('iframe',{attrs:{"src":"https://docs.google.com/presentation/d/e/2PACX-1vT7ZRiJRZz594YqNqFUgtkYilx3Y_mjmv6bztgaAo1AIPne7GY04ilbbpe-tWDHplDHZukXStqlQkTu/embed?start=true&loop=false&delayms=60000","frameborder":"0","width":"100%","height":"569","allowfullscreen":"true","mozallowfullscreen":"true","webkitallowfullscreen":"true"}}),_c('h2',[_vm._v("INFORMATION ARCHITECTURE")]),_c('p',[_vm._v(" After deciding to make an app as our product, we started brainstorming key features that would be the most helpful to our users. We utilized our design requirements and user personas when ideating the app’s features and how users would go through a specific task. Overall, our information architecture showed the relationships and flows between each feature. This set us up for success as we started ideating and creating paper prototypes of our app screens. ")]),_c('img',{attrs:{"src":"assets/night-owl/ia.png","alt":"information architecture"}}),_c('h2',[_vm._v("PAPER PROTOTYPING USABILITY TESTING")]),_c('p',[_vm._v(" The paper prototypes were a quick, easy way to test our designed features without distracting users with colors and photos. They helped us pinpoint confusing and unintuitive aspects of our initial features, which we incorporated into our redesign. First, we each individually sketched and described three interfaces. Then, we came back together and combined our rough draft sketches into consistent interface sketches for our usability tests. Based on our storyboards and information architecture diagram, we defined three key tasks that we asked users to complete in our tests. ")]),_c('p',[_c('em',[_vm._v("Task 1:")]),_vm._v("User searches for a final destination, which will be home. Then they will select the path that they want to take and start their route. ")]),_c('p',[_c('em',[_vm._v("Task 2:")]),_vm._v("User reports a burglary incident and inputs the correct information (date, time, description) and submits it. After submitting, check that it was successfully added to the map. ")]),_c('p',[_c('em',[_vm._v("Task 3:")]),_vm._v("User adds new safety contact and then calls them.")]),_c('h3',[_vm._v("Participants:")]),_c('p',[_vm._v(" We tested our prototype with four female University of Washington students who walk alone at night at least once a week. ")]),_c('h3',[_vm._v("Method:")]),_c('p',[_vm._v(" For each evaluation, we followed three major phases of testing in order to cohesively gather quality information about our users and our app. The first phase started off with an interview with each participant in order to gather basic demographic information. The second phase consisted of the main usability testing by providing users with a task that they had to accomplish while observers recorded notes. During the test, we asked participants to think out loud so that we could better understand their thought process and pain points. To make it seem like a real application, we used a Marvel App to conduct our usability testing. Lastly, we performed a post test interview to get final details of what participants thought of their experience of our solution to night safety. After individually completing our usability tests, we came back together to synthesize and analyze our findings. We found commonalities in the features users struggled with, strengths and weaknesses of our design, and suggestions for redesign. ")]),_c('h3',[_vm._v("Key Findings")]),_c('ul',[_c('li',[_vm._v(" Users wanted to be able to share their location even when they hadn’t started a path. ")]),_c('li',[_vm._v(" Users did not expect the app to immediately call a contact after clicking on their contact tile. ")]),_c('li',[_vm._v(" Users want to be able to quickly input information when reporting a hazard. ")]),_c('li',[_vm._v(" Users stated that hazards have different urgency levels, which should be reflected in the app. ")])]),_c('div',{staticClass:"d-flex flex-column"},[_c('a',{attrs:{"href":"https://drive.google.com/file/d/1qPIgWAavYumI0R3QtWMOUUZ1rtpOxKA_/view"}},[_vm._v("Find the full usability test document here")]),_c('a',{attrs:{"href":"https://drive.google.com/file/d/1wC734_OE-8ysEgzNrrHeQwgDfsHXpCfU/view"}},[_vm._v("Find the full interface sketches document here")])]),_c('h2',[_vm._v("REFINEMENTS")]),_c('p',[_vm._v(" Based on our findings from our usability tests we made some changes to our designs. Below are the three main changes that we made based on user feedback. ")]),_c('h3',[_vm._v("Report a Hazard:")]),_c('img',{attrs:{"src":"/assets/night-owl/report.png","alt":"report a hazard"}}),_c('p',[_vm._v(" Having different icon categories and hazard types was confusing, but users still wanted hazards to be differentiated in some way. We made a few changes based on this feedback: ")]),_c('ul',[_c('li',[_vm._v(" Removed different icon types and only allowed users to report hazards ")]),_c('li',[_vm._v("Removed hazard types")]),_c('li',[_vm._v("Allowed users to enter a title for the hazard")]),_c('li',[_vm._v("Added hazard levels (low, medium, and high danger)")])]),_c('h3',[_vm._v("Calling Safety Contact:")]),_c('img',{attrs:{"src":"assets/night-owl/call.png","alt":"calling a safety contact"}}),_c('p',[_vm._v(" When users tapped on a contact, they expected to be taken to an editing page instead of it calling them, so we: ")]),_c('ul',[_c('li',[_vm._v(" Added a “call” button so that users would know that tapping it would immediately call the contact ")]),_c('li',[_vm._v(" Tapping on the contact’s picture would bring them to the editing page ")])]),_c('h3',[_vm._v("Select a Route:")]),_c('img',{attrs:{"src":"assets/night-owl/route.png","alt":"selecting a route"}}),_c('p',[_vm._v("We made a few minor changes to this screen to alleviate confusion:")]),_c('ul',[_c('li',[_vm._v("Added “start route” button to collapsed menu")]),_c('li',[_vm._v("Moved “start route” button to the side")]),_c('li',[_vm._v("Added “From” text field at the top")])]),_c('h2',[_vm._v("ANNOTATED WIREFRAMES")]),_c('p',[_vm._v(" Also based on our usability testing feedback, we redesigned and added aspects to our wireframes. Then, looking back at our information architecture, we created the app’s full functionality in Figma. These wireframes were supposed to be low fidelity without color and real text in order to strictly communicate the app’s features. We annotated each screen to highlight key components and describe functionality. This step helped us think about how our features worked together and was a useful way to get feedback before designing our high fidelity prototypes. ")]),_c('iframe',{attrs:{"src":"https://docs.google.com/presentation/d/e/2PACX-1vRL2N2US5n7fW2v2BMZFqcVFdtpSxb_63q4bEmWDwWCgBMND7dLu54YJiiLi60cjkMb8bzZHOgr5VTT/embed?start=false&loop=false&delayms=60000","frameborder":"0","width":"100%","height":"569","allowfullscreen":"true","mozallowfullscreen":"true","webkitallowfullscreen":"true"}}),_c('h2',[_vm._v("HIGH FIDELITY PROTOTYPE")]),_c('p',[_vm._v(" Based on all our research and testing, we created an app called Night Owl, that students could use to feel safe when walking alone at night. The app has two main components: safety circle and map. ")]),_c('p',[_vm._v(" In the safety circle, based on interviewees stating they feel comfortable when they’re walking and talking to their friend, users are able to quickly and easily call their friends. If their friends are busy, users can call the bot. The bot will record and talk to them, mimicking a ‘friend’ listening to you in case your are in a dangerous situation. ")]),_c('p',[_vm._v(" On the map, users can select a destination and the safest path to get there. Once the user starts their route, their safety contacts will be alerted and the app will automatically send another notification when the user gets to their final destination; this happens automatically because it’s easy to forget to let your friends know you got home safe. To keep the incidents up to date on the map, we use crowdsourcing; users can report and confirm hazards. ")]),_c('p',[_vm._v(" After user feedback, we made the SOS button be easily clickable, which brings up a popup that counts down from 5 seconds and then calls 911. If the user accidently pressed the SOS button, they can easily cancel it. But we learned that it’s important the SOS button be very quick to use in case of a dire situation. ")]),_c('p',[_vm._v(" For our colors, we chose dark mode because users would be using this app at night, so we wanted the screen to be subdued. Our color scheme is dark blue, bright orange, and bright red. It is important to create high contrast to show key information. Orange represents important actionable items such as reporting a hazard and starting route. The bright red is used only for the SOS button to ensure it stands out in case of emergency. ")]),_c('iframe',{attrs:{"src":"https://docs.google.com/presentation/d/e/2PACX-1vRkHp-cF2YFbECqU7HqJx7N3RFlvbFBt5B0qjV56rOjoO5U2NFuqssJbG4mCIzL5zZhFWtvyG4Vd0LR/embed?start=false&loop=false&delayms=60000","frameborder":"0","width":"100%","height":"569","allowfullscreen":"true","mozallowfullscreen":"true","webkitallowfullscreen":"true"}}),_c('h2',[_vm._v("Video Demos of Night Owl:")]),_c('h3',[_vm._v("Add a Contact")]),_c('iframe',{attrs:{"width":"100%","height":"315","src":"https://www.youtube.com/embed/EXme5E_8iGg","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('h3',[_vm._v("Report A Hazard and Select a Route")]),_c('iframe',{attrs:{"width":"100%","height":"315","src":"https://www.youtube.com/embed/FqrJq6qdI88","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('h3',[_vm._v("Add New Location Favorite")]),_c('iframe',{attrs:{"width":"100%","height":"315","src":"https://www.youtube.com/embed/ycDCc9rzfPo","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('h2',[_vm._v("REFLECTION")]),_c('p',[_vm._v(" Overall, this project was challenging and definitely stretched our thinking in both design and teamwork. Working with a group to create a high-fidelity mockup for a product in just 11 weeks was difficult and required hard work from everyone. We learned that designing with a team requires clear and effective communication, compromise, and a lot of dedication. With that being said, we’re all proud of our final product and enjoyed every step of the design process. ")]),_c('ul',[_c('li',[_vm._v("More user research and usability testing")]),_c('li',[_vm._v(" Explore optimizing map path finding for users going between the same two places multiple times a week ")])]),_c('p',[_vm._v(" If we had more time, we definitely would have conducted more user research and usability testing. Specifically, it would have been useful to do competitive analysis on other technologies and services rather than just apps. Also, although a phone app fit our design requirements, we didn’t have much time to think about alternative forms of our final product. We definitely would’ve wanted to explore other options, especially since our user group of students walking at night should actually be discouraged from using their phones. ")]),_c('p',[_vm._v(" In addition, there were parts of our app that we want to test more because we are unsure of their effectiveness or usefulness. One of these functions is the SOS button; we had gotten a lot of feedback about how to make it easily accessible while not making it easy to accidentally trigger. Based on user feedback, we were able to incorporate a solution into our final design, but we would want to test with more users. However, it was difficult to actually test the true functionality of the SOS button because it’s meant to be used in emergency situations. Another feature that we were unsure about was the ability to call a Bot. Some of our users said that they would feel weird talking to a Bot and wouldn’t use it, while some said they would. With more time, we would’ve wanted to figure out how to make this feature truly useful to our users. ")]),_c('p',[_vm._v(" One really interesting question we received was about our research into how often our users were going between the same two points every week, and how we can optimize our map function to those users. This question was very thought-provoking; since one of our features was pathfinding on a map, we immediately turned to pre-existing solutions (e.g. Google Maps, Apple Maps) as a base. However, these solutions are made to deal with someone starting from anywhere and going to anywhere. Dealing with the situation of users going between the same two points multiple times a week might result in a completely different solution. If we had the time, we would want to explore optimization for this specific situation. ")]),_c('p',[_vm._v("Finally, here are some key takeaways from each of us:")]),_c('p'),_c('p',[_c('em',[_vm._v("Maya:")]),_vm._v(" At the end of the project, I was surprised by how much a “final” design can still be improved. It was a little disheartening seeing how much critique our (almost) final design received, but when I thought about it, pretty much every design in the world can be improved in some way. ")]),_c('p',[_c('em',[_vm._v("Valerie:")]),_vm._v(" This has been one of the most incredible design experiences I have had. I love working in groups to design artifacts rather than trying to design something complex like Night Owl on your own. I loved watching our project grow as we shared ideas and made compromises about our designs. Each member of the team brought something very unique to the table, and although it was challenging to incorporate what worked best for all of us, it was in this diversity of the designers in our group that we were able to produce a product that we are all proud of. ")]),_c('p',[_c('em',[_vm._v("Peter:")]),_vm._v(" Overall, this experience from start to finish has been memorable. I enjoyed collaborating with my teammates as we each had important contributions to Night Owl. As for the project itself, we may have finished the first iteration of the high fidelity prototype, but there is still much improvements we can make, especially after our final critique. ")]),_c('p',[_c('em',[_vm._v("Lia:")]),_vm._v("I learned how to optimize time and create quality work by understanding and using each team mate’s skills and passions. At different steps in our process, each one of us went above and beyond to help the project excel. ")]),_c('img',{staticClass:"mb-5",attrs:{"src":"assets/night-owl/team.png","alt":"the night owl team"}})])}
