var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('PortfolioPiece',[_c('div',{staticClass:"title"},[_c('h1',[_vm._v("Crochet Buddy")]),_c('p',[_vm._v(" Exploratory prototyping for Crochet Buddy: an Internet of Things (IoT) device created to assist novice crochet hobbyists in the pursuit of beautiful yarn-craft. ")])]),_c('hr'),_c('p',[_vm._v(" In the Spring of 2020, around the start of the Novel Coronavirus Pandemic that caused countries to shut down, I took up the art of crochet to fill the days in isolation. Due to my inability to count my stitches correctly in each of the rows I completed, the first blanket I made came out as more of a trapezoid than a rectangle. The other big issue that most novice crochet hobbyists is keeping a consistent tension in the amount that you pull on the yarn to create each stitch. Crochet Buddy is an Internet of Things (IoT) device created to assist novice crochet hobbyists in their pursuit of beautiful yarn-craft. This project consisted of exploration in: analog prototyping, 3D modeling, Voice User Interface, and AI stitch tracking. ")]),_c('img',{staticClass:"p-0",attrs:{"src":"assets/crochet-buddy/sketch.png","alt":"crochet buddy sketch"}}),_c('h2',[_vm._v("Design Overview")]),_c('p',[_vm._v(" The Crochet Buddy is a fairly simple IoT device with a large impact on those who crochet. The physical device has three main components: an ergonomic handle, a camera facing the crochet hook’s neck, which counts the number of stitches and tracks which type of stitches are being made using a machine learning algorithm. The hook also has a pressure sensitive hook, which tracks the amount of force exerted each time the user pulls for the stitch. The hook will also vibrate when the user should stop pulling on the stitch, thus enforcing a consistent pull, making the yarn-craft more consistent. ")]),_c('iframe',{attrs:{"width":"100%","height":"315","src":"https://www.youtube.com/embed/X2v73GxMAIo","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('h2',[_vm._v("Analog Prototype")]),_c('p',[_vm._v(" The analog prototype for Crochet Buddy was made out of 3 ingredient clay (made with flour, water, and salt) as well as a crochet hook that I had lying around my room. I made the clay for the hook, and then wrapped it around my hook around to form a handle. I then took and extra bit of clay to figure out what the camera placement would look like. After the handle was formed, I took it off of my crochet hook, and baked it until the clay became sturdy. I then put the handle back on my crochet hook for user testing. The first handle iteration is shown below. ")]),_c('img',{staticClass:"pt-0",attrs:{"src":"assets/crochet-buddy/analog_1.jpeg","alt":"crochet buddy sketch"}}),_c('p',[_vm._v(" I actually went through two prototypes while designing this hook. The first prototype turned out to be far too bulky and big, so I created a sleeker and smaller handle, which ended up being the better of the two, as shown below. ")]),_c('img',{staticClass:"pt-0",attrs:{"src":"assets/crochet-buddy/analog_2.jpeg","alt":"crochet buddy sketch"}}),_c('b',[_vm._v("Analysis")]),_c('p',[_vm._v(" The grip that I created for my prototype makes holding the crochet hooks that I have so much more comfortable, that I actually find myself using the lo-fi prototype handle when I’m planning on crocheting for longer periods of time. This is a testament to the biggest feature that I was using my analog model to test for: comfort and grip positioning. ")]),_c('p',[_vm._v(" During the analysis phase, I also took a video of what the camera would see. This was to see if it was even possible to use machine learning to track stitches in this way. It did prove that there is a distinct possibility that machine learning for stitch counting in this way is a distinct possibility. ")]),_c('h2',[_vm._v("3D Prototyping")]),_c('p',[_vm._v(" The 3D model of Crochet Buddy was made in Onshape: a collaborative CAD modeling program. I began the model as a sketch the front plane. This sketch contained a half view of the side profile of Crochet Buddy’s main form. As you can see, the sketch captures the rounded features of crochet buddy which make it more comfortable to use. ")]),_c('img',{attrs:{"src":"assets/crochet-buddy/3d_sketch.png","alt":"line sketch for crochet buddy"}}),_c('p',[_vm._v(" Next, I use the revolve feature for the above sketch, to create the base for Crochet Buddy. this lead to a very simple three-dimensional figure that works as the base for Crochet Buddy. ")]),_c('img',{attrs:{"src":"assets/crochet-buddy/3d_round.png","alt":"revolve for crochet buddy"}}),_c('p',[_vm._v(" Once the main 3D form for Crochet Buddy was completed, I created a new sketch, again on the front plane. These sketches are on the same plane to ensure that the sketches overlap and can actually be used for extrudes. ")]),_c('img',{attrs:{"src":"assets/crochet-buddy/3d_cut.png","alt":"sketch for crochet hook cut"}}),_c('p',[_vm._v(" In order to create the cutout that makes up the hook for Crochet Buddy, I used an extrude cut. The extrude cut took the material from the triangle that I had sketched away from the hook. The more angular slanted nature of the cutout for Crochet Buddy is based off of the angular style of Susan Bates crochet hooks, which are objectively the easiest to work with. Most others are too rounded, which makes it difficult to capture the loops that are being pulled through to create a stitch. ")]),_c('img',{attrs:{"src":"assets/crochet-buddy/3d_clear.png","alt":"cut for crochet buddy hook"}}),_c('p',[_vm._v(" For the third time, I created a sketch on the front plane. This time, it was nestled against the thick grip section of the handle, on top of the beginning of the hook. This is the ideal placement because it is out of the way for the user, and it has a pretty solid view down the neck of the hook, which is the vantage point used for counting and tracking stitches. ")]),_c('img',{attrs:{"src":"assets/crochet-buddy/3d_camera.png","alt":"sketch for crochet buddy camera"}}),_c('p',[_vm._v(" To create the camera housing, based off of the initial sketch, I used an extrude on the square. I extruded both to the left and to the right with the sketch on the front plane as my “center point.” This allowed me to make the camera housing evenly centered on the hook so that the camera would have the clearest view down the hook’s neck. ")]),_c('img',{attrs:{"src":"assets/crochet-buddy/3d_cam_col.png","alt":"extrude for crochet buddy camera"}}),_c('p',[_vm._v(" Finally, to make the the 3D model more aesthetically pleasing and appear to be more realistic, I used the appearance feature of Onshape to apply a blue color to the hook part of Crochet Buddy. This blue color mimics the standard color for 5mm crochet hooks. ")]),_c('img',{attrs:{"src":"assets/crochet-buddy/3d_color.png","alt":"final model for crochet buddy"}}),_c('div',{staticClass:"text-center"},[_c('a',{attrs:{"href":"https://cad.onshape.com/documents/6fe367458e9e7df097cd1b08/w/c87550b82174ec9333dfbd76/e/b54d1e8d204f7130c8ada965","target":"_blank"}},[_vm._v("You can view the 3D model here.")])]),_c('b',[_vm._v("Analysis")]),_c('p',[_vm._v(" Because I’ve taken classes on CAD modeling before, I am fairly content with the decisions that I made to create the 3D model for Crochet Buddy. The 3D model created in Onshape resembles my analog model very nicely. The only real downfall of my prototype is that it is not fully constrained. But, as you can see, the dimensions of the model appear to be correct. ")]),_c('p',[_vm._v(" Overall, I am incredibly proud of the 3D model for Crochet Buddy. but there are definitely still areas for improvement. In the future, I would probably do some deeper multi-piece assemblies with constraints in the button interactions. I would include a button that would be used to “indicate the end of the first line of stitches, and any other changes made by the user while working on their piece. This would be located on the trip of the handle. I would also possibly add a long thin button going down the entire length of the hook, that would actually be what was used to gauge the pressure in the pulls for each stitch. ")]),_c('h2',[_vm._v("Voice User Interface")]),_c('p',[_vm._v(" Although Crochet Buddy has functionality for a crotchetier of any skill levels, this iteration focuses on Crochet Buddy as a resource for total beginners to learn crochet with real time feedback. Never again will a novice crochetier be left wondering if they missed a step, misunderstood the directions, or did something wrong. The design for the educational aspect of crochet buddy has two main aspects: ")]),_c('ul',[_c('li',[_vm._v(" An updated analog prototype which includes the ability to attach an actual web camera to Crochet Buddy. ")]),_c('li',[_vm._v(" The VUI which prompts users step by step how to crochet, by explaining the steps to make a variety of stitches. ")])]),_c('b',[_vm._v("Goal")]),_c('p',[_vm._v(" From the start of my exploration into the development of Crochet buddy, I had the idea in my head that novice a crochetier would be able to use it as a platform to learn crochet, and to follow along with patterns from the internet. The goal of this iteration is to gauge both the usability and feasibility of learning using only a VUI alone, albeit one paired with stitch tracking capabilities. ")]),_c('b',[_vm._v("Analog Prototype")]),_c('p',[_vm._v(" The analog model of Crochet Buddy followed a similar form to the original Crochet Buddy hook. The biggest factor taken into account in this model was the idea that I wanted the camera to have a full view of all of the hook that was open to yarn. But, the focal length of my web camera was super short, which meant that I had to cover a very large amount of the hook in order for the camera to have this full perspective of the yarn on the hook. ")]),_c('img',{staticClass:"pt-0",attrs:{"src":"assets/crochet-buddy/vui_analog.png","alt":"analog model for crochet buddy vui"}}),_c('p',[_vm._v(" Although the appearance of the new hook and handle is a lot bulkier, due to my constraints, it definitely shows a very clean video feed. In the video below, watch a user alternate between a single and double croceht stitch from Crochet Buddy's perspective. ")]),_c('iframe',{staticClass:"pb-1",attrs:{"width":"100%","height":"315","src":"https://www.youtube.com/embed/hkRtghFuSuI","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('p',[_vm._v(" In future iterations (with a higher budget), I would look into smaller, wireless cameras with a wider lens to be integrated into a the hook in a cleaner fashion. ")]),_c('b',[_vm._v("VUI Interaction")]),_c('p',[_vm._v(" Below, you can find the scripts for the VUI interactions that have been crafted for each of the stitches for the crocheter to learn. This script goes over a whole tutorial, teaching the user the chain stitch, the double crochet, and the triple crochet. But, these can also be taught individually upon request. ")]),_c('p',[_c('b',[_vm._v("USER:")]),_vm._v(" Crochet Buddy, I want a crochet tutorial.")]),_c('p',[_c('b',[_vm._v("CB:")]),_vm._v(" Start.")]),_c('b',[_vm._v("Chain Stitch")]),_c('iframe',{staticClass:"pb-1",attrs:{"src":"https://docs.google.com/presentation/d/e/2PACX-1vS57b9b9vWJZdVvoVI8aO0s2QSPVGMXMa1eADhaCUYHp60UC7zbC2Q-4Mo9CsFj7QMuqJVXrLycQiUD/embed?start=false&loop=false&delayms=3000","frameborder":"0","width":"100%","height":"350","allowfullscreen":"true","mozallowfullscreen":"true","webkitallowfullscreen":"true"}}),_c('p',[_c('b',[_vm._v("CB:")]),_vm._v(" To begin, let’s make a slip knot. Loop the yarn around your index finger, then grab some yarn from between the loop you made and 