het. To get familiar with this stitch, repeat it 9 more times until the end of your row. Remember, the next chain that you put your hook through should be the one right next to your last stitch. To hear the stitch instructions again say “one more time.” ")]),_c('ul',[_c('li',[_c('b',[_vm._v("(1) USER:")]),_vm._v(" One more time. ")]),_c('li',[_c('b',[_vm._v("CB:")]),_vm._v(" Tightly loop the yarn around your crochet hook. ")]),_c('li',[_c('b',[_vm._v("USER:")]),_vm._v(" (has two loops on the hook). ")]),_c('li',[_c('b',[_vm._v("CB: ")]),_vm._v(" Now, take your hook and put it through the chain closest to the last stitch you made. ")]),_c('li',[_c('b',[_vm._v("USER:")]),_vm._v(" (has three loops on the hook). ")]),_c('li',[_c('b',[_vm._v("CB: ")]),_vm._v(" Once again, tightly loop the yarn around the crochet hook. ")]),_c('li',[_c('b',[_vm._v("USER:")]),_vm._v(" (has four loops on the hook). ")]),_c('li',[_c('b',[_vm._v("CB: ")]),_vm._v(" Now pull the loop closest to the hook mouth through the two loops between the first and last loop on the hook. ")]),_c('li',[_c('b',[_vm._v("USER:")]),_vm._v(" (has two loops on the hook). ")]),_c('li',[_c('b',[_vm._v("CB: ")]),_vm._v(" Once again, tightly loop the yarn around the crochet hook. ")]),_c('li',[_c('b',[_vm._v("USER:")]),_vm._v(" (has three loops on the hook). ")]),_c('li',[_c('b',[_vm._v("CB: ")]),_vm._v(" Now pull the loop closest to the hook mouth through the other two loops on the hook. ")]),_c('li',[_c('b',[_vm._v("USER:")]),_vm._v(" (has one loops on the hook). ")]),_c('li',[_c('b',[_vm._v("CB: ")]),_vm._v(" Congratulations. You’ve completed your Xth double crochet. To get familiar with this stitch. To hear the stitch instructions again say “one more time.\" ")])]),_c('p',[_c('b',[_vm._v("USER:")]),_vm._v(" (reaches 9 more double crochets).")]),_c('p',[_c('b',[_vm._v("CB:")]),_vm._v(" Awesome! You have learned the double crochet. You are now at the point where you can start making crochet items!")]),_c('div',{staticClass:"text-center"},[_c('a',{attrs:{"href":"https://docs.google.com/document/d/1sw-pY2WbI3VCdQqk_Mk5MY1IYT0yUQpTpH7eHamClxk/edit?usp=sharing","target":"_target"}},[_vm._v("You can view the document with all the VUI scripts here.")])]),_c('b',[_vm._v("Demo")]),_c('p',[_vm._v(" The final aspect of Crochet Buddy was the demo video, which showcases the VUI and stitch tracking camera working together to deliver an easy and seamless way to learn crochet! ")]),_c('iframe',{attrs:{"width":"100%","height":"315","src":"https://www.youtube.com/embed/pvUAqp3lwj4","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('b',[_vm._v("Evaluation")]),_c('p',[_vm._v(" When I was initially thinking about Learning with Crochet Buddy, I wanted to include a Graphical User Interface (GUI) with the VUI so that the user would be able to see what they are supposed to be doing, in addition to hearing it from the VUI, while they are doing it. But, I also wanted to see how feasible it would be to learn Crochet by voice alone. To test the VUI’s effectiveness on it’s own, I ran a VUI usability test with my mom over Zoom. Because the Crochet Hook that my mom was using was poorly made and almost impossible to use, we decided to skip over the triple crochet section of the tutorial script. ")]),_c('iframe',{attrs:{"width":"100%","height":"315","src":"https://www.youtube.com/embed/02jf-K-xQZQ","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('p',[_vm._v(" The main findings from this evaluation was that knowing the number of stitches that need to be on the hook is important information for the user, when they are learning any crochet stitch, even if Crochet Buddy is tracking it for them. It seems to be critical to the learning process for the user to either have a view of what the state of the yarn should look like as a picture, or to know the number of loops that should be on the hook at any given time. ")]),_c('p',[_vm._v(" It also became incredibly apparent that in any future iterations of Crochet Buddy it is important to only use the "),_c('a',{attrs:{"href":"https://www.amazon.com/Susan-Bates-Silvalume-Crochet-Pouch/dp/B000XZYISM","target":"_blank"}},[_vm._v("Susan Bates Hooks")]),_vm._v(", which are the most well loved hooks in the world of crochet. The other generic crochet hooks just did not work very effectively, and made it incredibly difficult for my mother to even attempt the steps Crochet Buddy was giving her. ")]),_c('b',[_vm._v("Analysis")]),_c('p',[_vm._v(" Although the analog prototype shows how effective the vantage point of having a camera embedded in the hook of Crochet Buddy’s handle is, the web camera that I used for this analog prototype proved to be bulky and had a very narrow view point. The bulky clip on the camera made it very difficult to hold the analog prototype, while the narrow camera angle made it so that there could only be a very small portion of the hook open for yarn in order for the camera to view all the loops on the hook. ")]),_c('p',[_vm._v(" The next steps in furthering the Crochet Buddy analog design would be the use of a smaller camera with a wider lens that does not interfere with the quality and comfort of the handle grip of Crochet Buddy. It would be mounted in such a way where it would be angled to see a bigger portion of the crochet hook. ")]),_c('p',[_vm._v(" The VUI combined with stitch tracking also hold promise. From the demo video, it is clear that having live feedback from Crochet Buddy, with limited vocal interactions, could be an easy and fun way to learn crochet. The biggest change to the VUI would be to add an additional sentence to each of the lines that the VUI gives, stating the number of loops that should be on the hook at any given time. Although I thought having Crochet Buddy’s stitch tracking would be enough to help the user, my testing it proved that the user would like to know the number of loops that should be on the hook. Having the VUI state the number of loops there should be proves effective then it would be able to be distributed without connection to an app. Future iterations of Crochet Buddy would also instructions for more stitches than the three stitches used for this prototype. I would also change the script to possibly include more crochet specific terms. When I was writing my scripts this time around I was trying to use words that maybe were more descriptive, but given my user testing and evaluation I believe having the more specific terms would be more effective, especially if there is a way for users to interact with Crochet Buddy to find out the definitions of the more niche crochet terms if they aren’t obvious enough. ")]),_c('p',[_vm._v(" The next step for Crochet Buddy’s learning and educational systems would be the creating of a mobile application which would integrate a GUI into the pre-existing VUI and stitch tracking capabilities of Crochet Buddy. This would enable users to have both audio and visual instructions in their real time feedback. This application interface would also make it possible t get rid of the audio feedback entirely and focus on written or picture instructions which use on screen prompts and stitch tracking to provide live feedback even when the user doesn’t want audio. ")]),_c('div',{staticClass:"d-flex flex-column flex-md-row text-center justify-content-between"},[_c('router-link',{attrs:{"to":"/common-ground"}},[_c('b',[_vm._v("Previous Project: ")]),_vm._v(" Common Ground")]),_c('router-link',{attrs:{"to":"/nature"}},[_c('b',[_vm._v("Next Project: ")]),_vm._v("Access to Nature and Crime")])],1)])}
