var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('PortfolioPiece',[_c('div',{staticClass:"title"},[_c('h1',[_vm._v("OnTrak Troubleshooting")]),_c('p',[_vm._v(" Empowering users to resolve product conflicts without calling for help ")]),_c('hr')]),_c('p',[_vm._v(" During the summer of 2021, I was given the opportunity to intern at Digital Control Inc. (DCI), a company that specializes in locating technology for horizontal directional drilling (HDD). Their tool set tracks the location of the drill head underground through a two part hardware system consisting of a transmitter and a locator. These systems exhibit issues are fairly common, and the customer service department has standardized procedures for troubleshooting over the phone. For this project, I was tasked with creating a troubleshooting application that would empower users to resolve product issues without having to call customer service for assistance. My design focused on the most common issue, \"No Data on Locator\", where the locator does not show the location information for the underground transmitter. ")]),_c('h2',[_vm._v("Competitive Analysis")]),_c('p',[_vm._v(" To gain an understanding of the problem space, I began with competitive analysis. ")]),_c('b',[_vm._v("Sites Analyzed")]),_c('ul',[_c('li',[_vm._v(" DCI’s own helpdesk site")]),_c('li',[_vm._v(" DCI’s current product manual/helpdesk mobile application")]),_c('li',[_vm._v(" Apple’s helpdesk site")]),_c('li',[_vm._v("Ford’s helpdesk site")]),_c('li',[_vm._v(" iFixIt’s crowdsourced troubleshooting site ")])]),_c('b',[_vm._v("Key Findings")]),_c('ul',[_c('li',[_vm._v(" Current solutions tend to be overwhelmingly text heavy ")]),_c('li',[_vm._v("Use links and subheadings as the main method of navigation ")]),_c('li',[_vm._v("It can be difficult for users to translate text into concrete actions to be applied to the hardware system")])]),_c('b',[_vm._v("Solution Implications")]),_c('ul',[_c('li',[_