ight occur on the job. ")]),_c('p',[_vm._v(" The high-fidelity prototype allows you to walk through the multiple pathways involved in this user-driven problem-solving process for the “No Data on Locator” flow as the user is walked through a choose-your-own-adventure styled interaction. ")]),_c('p',[_vm._v("Use the prototype below to explore the interactions for yourself!")]),_c('iframe',{staticStyle:{"border":"1px solid rgba(0, 0, 0, 0.1)"},attrs:{"SameSite":"None","width":"100%","height":"450","src":"https://www.figma.com/embed?embed_host=share&url=https%3A%2F%2Fwww.figma.com%2Fproto%2FOv0FRMitxrKGHictBHTxMe%2FonTrak-(Copy)%3Fpage-id%3D2%253A1881%26node-id%3D2%253A1881%26viewport%3D520%252C291%252C0.1%26scaling%3Dscale-down%26starting-point-node-id%3D2%253A8209","allowfullscreen":""}}),_c('h2',[_vm._v("USER TESTING")]),_c('p',[_vm._v(" After several iterations of prototyping, it was time to test the troubleshooting application with those who would be using it on the drill site. Due to COVID-19 restrictions, these user tests were completely virtual. ")]),_c('p',[_c('b',[_vm._v("Participants: ")]),_vm._v(" We tested the high-fidelity prototype with 11 in house participants in the form of both field representatives, and customer service representatives. ")]),_c('p',[_c('b',[_vm._v("Purpose: ")]),_vm._v(" To gain perspective on the merit of the onTrak application based on those who work with customers daily. ")]),_c('p',[_c('b',[_vm._v("Task: ")]),_vm._v(" Users followed the steps in the workflow from selecting equipment and changing bands all the way through calibration. ")]),_c('p',[_vm._v(" After completing the steps involved in the user testing task, we asked participants 3 Likert scale questions to assess the app’s usability. On a scale from 1 to 5, with 1 being the lowest/worst score, and 5 being the highest/best score, they rated: how useful the application was, how they enjoyed using the application, and it’s usefulness in the field. As you can see, each of these 3 categories found ratings above 4.5/5. ")]),_c('img',{attrs:{"src":"assets/on-trak/rating.png","alt":"ratings"}}),_c('p',[_vm._v(" As far as critical feedback goes, I sorted my results into what worked, and what could be improved upon as shown below: ")]),_c('h3',[_vm._v("What Worked:")]),_c('ul',[_c('li',[_vm._v("Clean UI.")]),_c('li',[_vm._v("Straightforward instructions that are nice and simple.")]),_c('li',[_vm._v("Felt like a game.")]),_c('li',[_vm._v("Helps focus amid the large amounts of data on the locator.")]),_c('li',[_vm._v(" Diagrams are easier than trying to describe it over the phone. Dummy-proof. ")])]),_c('h3',[_vm._v("What Worked:")]),_c('ul',[_c('li',[_vm._v(" Testers would have liked a locator in their hands while testing (an affordance that testing online due to COVID-19 did not allow for). ")]),_c('li',[_vm._v("More explanation as to why they are doing each step.")]),_c('li',[_vm._v(" Ability to check for electrical failure upon closing drill head compartment. ")]),_c('li',[_vm._v("More free form exploration.")]),_c('li',[_vm._v(" Integration into Digi Guide, the current application for troubleshooting, and manual reference. ")])]),_c('h2',[_vm._v("REFLECTION")]),_c('p',[_vm._v(" This project was a lot of fun, it was an incredible learning opportunity for how to create mobile applications with a more gamified look and feel, which helps reduce the tedium in everyday life. Although most sites that cover step-by-step instructions tend to have a long scrolling experience, the shift to small, quickly read pages hat users could swipe though shifted the experience to an entirely new level. This focus on simple, and clean UI will be a focus as I continue to design more applications. ")]),_c('div',{staticClass:"d-flex flex-column flex-md-row text-center justify-content-between"},[_c('router-link',{attrs:{"to":"/fin-lit"}},[_c('b',[_vm._v("Previous Project: ")]),_vm._v(" Chime Financial Literacy")]),_c('router-link',{attrs:{"to":"/common-ground"}},[_c